.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Global Styles */

.logo-font {
  font-family: "Black Han Sans", sans-serif;
}

.greenBlue-gradient {
  background-color: #08aeea;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#08aeea), to(#2af598));
  background-image: -webkit-linear-gradient(bottom, #08aeea 0%, #2af598 100%);
  background-image: -o-linear-gradient(bottom, #08aeea 0%, #2af598 100%);
  background-image: linear-gradient(0deg, #08aeea 0%, #2af598 100%);
}

.card-grey-text {
  color: #585858;
}

.dropShadow {
  -webkit-box-shadow: 3px 3px 5px 0px rgba(50, 50, 50, 0.64);
  box-shadow: 3px 3px 5px 0px rgba(50, 50, 50, 0.64);
}



